<style lang="scss">
#lineMap{
  width: 100%;
}
</style>
<template>
    <div id="lineMap"></div>
</template>
<script type="text/ecmascript-6">
import Map from '../../../../../assets/js/map'
import PointLine from '../../../../../assets/js/pointLine'
export default {
  data () {
    return {
      // page: "Map"
      showMap: new Map(),
      pointLinelist: null
    }
  },
  methods: {
    getDataPoint () {
      axios.get("../../../../../../static/libs/pointLine.json").then((res) => {
        this.pointLinelist = res.data.ip;
        var line = new PointLine(this.showMap, this.pointLinelist)
      })
    }
  },
  mounted () {
    this.getDataPoint()
    this.showMap.map.setTarget('lineMap')
  }
}
</script>



